<template>
  <div class="search-suggest">
      <h3 class="suggest-title">搜索"{{keywords}}"</h3>
      <div class="">
      
      <ol class="suggest-list">        
          <li v-for="(item,index) in suggestSongs" :key="item+index">
              <i class="list-svg"></i>
              <div class="list-text" @click.stop="$emit('suggestWord',item.keyword)">{{item.keyword}}</div>
          </li>
      </ol>
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
    props:{
       keywords:{
           type:String,
           default:''
       }
    },
    data() {
        return {
            suggestSongs:[]
        }
    },
created(){
    this.getSearchSuggest();
},
methods: {
    async getSearchSuggest(){
        await this.axios.get(`/search/suggest?keywords=${this.keywords}&type=mobile`).then(re=>{
            this.suggestSongs=re.result.allMatch
            // console.log(this.suggestSongs);
        })
    }
},
watch: {//监测keywords变化时,调用函数,获取提示
       // 观察的对象+(){} 函数
    keywords:_.debounce(function(){
        this.getSearchSuggest();
    },1000)
    
 }
}
</script>

<style lang="scss" scoped>
.search-suggest{
 .suggest-title{
    height: 50px;
    margin-left: 10px;
    padding-right: 10px;
    font-size: 15px;
    line-height: 50px;
    color: #507daf;
    border-bottom: 1px solid #eff0f1;
    font-weight: normal;
  }
  .suggest-list{
   
        li{
             width: 100%;
                height: 45px;
                line-height: 45px;
            position: relative;
             .list-svg{
                  position: absolute;
                   top: 50%;
                   left: 10px;
                   transform: translateY(-40%);
                     width: 15px;
                     height: 15px;
                  background-image: url(data:image/svg+xml;base64,);

    }
   
        .list-text{
                  width: 100%;
                  font-size: 15px;
                  margin-left: 30px;
                   border-bottom: 1px solid #eff0f1;
        }
                    
    }
 }
}
 
</style>