<template>
  <div class="search-result" >
      <div class="best-match" @click.stop="isfull=true">
      <span class="best-title" v-if="bestMatch.length>0">最佳匹配</span>
      <div class="best-gs" @click.stop="GetBestId(b.id)" v-for="b in bestMatch" :key="b.id">
          <img class="best-tx" :src="b.picUrl" alt="">
         歌手: {{b.name}}
         <div class="best-arr1"></div>
         </div>
      <div class="best-zj"></div>
      </div>
      <div class="singer-view" v-for="b in bestMatch" :key="b.id">
     </div>
      <music-list v-if="showList.length>0" :musiclist="showList" @play="$emit('play',$event)"></music-list>
     <load-animate v-else></load-animate>
     <router-view></router-view>
    <div class="loadmore" ref='loadmore'>{{page?'正在努力赶来ing...':"没有更多啦(●'◡'●)"}}</div>
  </div>
</template>

<script>
import MusicList from '@/components/MusicList.vue'
import LoadAnimate from '@/components/LoadAnimate.vue'

export default {
    props:{
    keywords:{
        type:String,
        default:''
    }
    },
data() {
    return {
       resultSongs:[],
       bestMatch:[],
       isfull:false,
       page:1
    }
},
created(){
 this.getSearchResult();
 this.getBestMatch();
},
methods: {
     windowScroll(){
     let scrollTop=document.documentElement.scrollTop||document.body.scrollTop;
     let loadMoreoffsetTop=this.$refs.loadmore.offsetTop;
     let clientHeight=window.innerHeight;
    //  console.log(scrollTop,loadMoreoffsetTop,clientHeight);
     if(scrollTop+clientHeight>=loadMoreoffsetTop+33){
       this.page++;
     }
   },
    async getSearchResult(){
        await this.axios.get(`/cloudsearch?keywords=${this.keywords}`).then(re=>{
            this.resultSongs=re.result.songs.map(item=>{
                item.song={
                     alias:item.alia,
                    artists:item.ar,
                    album:item.al,
                }
                return item;
            })   
            // console.log(this.resultSongs);
        })
    },
  async getBestMatch(){
      this.setHistory();
      await this.axios.get(`/search/multimatch?keywords=${this.keywords}`).then(re=>{
            this.bestMatch=re.result.artist
            // console.log('bestMatch=>>',this.bestMatch);
     })
  },
  setHistory(){
     if(!this.keywords||this.keywords==''||this.keywords.length<1){
         return
     }
     let historys=localStorage.getItem("searchhistory") ;
     let historyArr=[];
     if(historys){
         historyArr=JSON.parse(historys);
     }
    historyArr.unshift(this.keywords);
     let setArr=new Set(historyArr);
       console.log(setArr);
       historyArr=[...setArr]
       localStorage.setItem("searchhistory",JSON.stringify(historyArr))
  },
  GetBestId(id){
    this.$router.push({
         name:"SingerView",
      params:{
        id,
      }
    })
  }   
},
mounted(){
   window.addEventListener('scroll',this.windowScroll);
},
computed: {
    showList(){
     let end=this.page*20;
       return this.resultSongs.slice(0,end);
    }
},
components:{
    MusicList,
    LoadAnimate,
},
beforeDestroy(){
   window.removeEventListener('scroll',this.windowScroll);
 },
}
</script>

<style lang="scss" scoped>
.search-result {
    .best-gs{
        height: 60px;
        display: flex;
        line-height: 60px;
        margin-top: 5px;
        
        position: relative;
        .best-tx{
           margin-left: 5px;
           display: block; 
           padding-bottom: 5px;
           padding-right: 10px;
        }
        .best-arr1{
        width: 8px;
        height: 13px;
        background-image: url(data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgMCAxNiAyNiI+PHBhdGggZmlsbD0ibm9uZSIgc3Ryb2tlPSIjOWI5YjliIiBzdHJva2Utd2lkdGg9IjMiIHN0cm9rZS1saW5lY2FwPSJzcXVhcmUiIHN0cm9rZS1taXRlcmxpbWl0PSIxMCIgZD0iTTIuOTM5IDIzLjkzOUwxMy44NzkgMTMgMi45MzkgMi4wNjEiLz48L3N2Zz4=);
        background-repeat: no-repeat;
        background-position: center center;
         position: absolute;
         top: 20px;
         right: 15px;
            }
    }
.best-match {
    .best-title {
    margin-left: 10px;
    font-size: 12px;
    line-height: 16px;
    color: #666;
        
    }

  }
  .loadmore{
   text-align: center;
   font-size: 14px;
   line-height: 40px;
 }
}
 
</style>