<template>
<div class="search-hot">
   <h3>热门搜索</h3>
  <ul class="hot-list">
   <li @click.stop="$emit('searchWord',word.first)" v-for="(word,index)  in  hotSongs" :key="index">{{word.first}}</li>
  </ul>
  <ol class="search-history">
    <li>
     <div v-for="h in historyArr" :key="h" class="history-text" @click.stop="searchWord($event,h)">
       <i class="history-svg-1"></i>
       {{h}}
       <i class="history-svg-2" @click.stop="clearHistory(h)"></i>
     </div>
    </li>
  </ol>
</div>
</template>

<script>
export default {
  props:{
       keywords:{
           type:String,
           default:''
       }
  },
 data() {
        return {
          hotSongs:[],
          historyArr:[]  
        }
    },
    created(){
    this.getHotSearch();
    },methods: {
        async getHotSearch(){
        await this.axios.get('/search/hot').then(re=>{
        this.hotSongs=re.result.hots
        this.getHistory()
        })
    },
    getHistory(){
      let historyword=localStorage.getItem("searchhistory");
      if(historyword){
       this.historyArr=JSON.parse(historyword);
     }
    },
    searchWord(event,word){
    this.$emit('searchWord',word);
    },
    clearHistory(word){
     let historywords=localStorage.getItem("searchhistory");
     historywords=JSON.parse(historywords).filter(v=>{
      return v!=word;
     })
     this.historyArr= historywords;
    localStorage.setItem("searchhistory",JSON.stringify(historywords));
    }
    }
}
</script>

<style lang='scss' scoped>
.search-hot{
   margin-top: 20px;
   background-color: white;
    h3{
            font-size: 12px;
            line-height: 12px;
            color: #666;
            display: block;
            text-indent: 1rem;
            
      }
    .hot-list{
        margin-top: 10px;
        margin-left: 5px;
        li{
        display: inline-block;
        height: 32px;
        margin-right: 8px;
        margin-bottom: 8px;
        padding: 0 12px;
        font-size: 14px;
        line-height: 32px;
        color: #333;
        border: 2px solid #eeeff2;
        border-radius: 20px;
         }
        }
        .search-history{
              li{ 
                position: relative;
              
                height: 45px;
                line-height: 45px;
               
                .history-text{
        .history-svg-1{
                   position: absolute;
                   top: 50%;
                   left: -20px;
                   transform: translateY(-40%);
                     width: 15px;
                     height: 15px;
                     background-image: url(data:image/svg+xml;base64,);
               }
                  font-size: 14px;
                  margin-left: 30px;
                   border-bottom: 1px solid #eff0f1;
                   position: relative;
                }
                .history-svg-2{
                  width: 12px;
                  height: 12px;
                  background-image: url(data:image/svg+xml;base64,);
                  position: absolute;
                  top: 50%;
                  right: 15px;
                }
              }
        }
    }
</style>