<template>
  <div class="Search">
   <div class="searchInput">
     <i class="search-svg"></i>
   <input type="text" v-model="keywords" @keydown.enter="displayResult" @input.stop="inputing" placeholder="搜索歌曲、歌手、专辑">
   <i class="search-clear" v-show="keywords.length>0" @click.stop="keywords=''"></i>
   </div>
   <hr>
   <!-- 动态组件 -->
  <component @searchWord="searchWord" :keywords='keywords' :is="componentName" @play="$emit('play',$event)" @suggestWord='suggestWord' @historyWord='historyWord'></component>
  </div>
</template>

<script>
import SearchHots from '@/components/SearchHots.vue'
import SearchResult from '@/components/SearchResult.vue'
import SearchSuggest from '@/components/SearchSuggest.vue'
export default {
  data() {
    return {
    // 设置动态组件默认显示
      componentName:"SearchHots",
      keywords:'',
      
    }
  },
  components: { 
    SearchHots,SearchResult,SearchSuggest
   },
   methods: {
     inputing(){
    if(this.keywords!=''||this.keywords.length>0){
      this.componentName="SearchSuggest"
    }else{
      this.componentName="SearchHots"
    }
     },
     displayResult(){
      this.componentName="SearchResult"
     },
     searchWord(hotword){
        this.keywords=hotword;
        this.displayResult();
     },
     historyWord(hw){
       this.keywords=hw;
        this.displayResult();
     },
      suggestWord(sw){
       this.keywords=sw;
        this.displayResult();
     }
   },
   watch: {
      keywords:{
       deep:true,
       handler(){
        this.inputing();
       }
      }
   },  
}
</script>

<style lang="scss" scoped>
.Search{
    .searchInput{
        position: relative;
        height: 30px;
        width: 90%;
        padding-top: 15px 10px;
        margin-left: 20px;
        background-color: #ebecec;
        border-radius: 15px;
         .search-svg{
        position: absolute;
        left: 0;
        top: 9px;
        margin: 0 8px;
        vertical-align: middle;
        width: 13px;
        height: 13px;
        background-image: url(data:image/svg+xml;base64,);
      }
        input{
        height: 30px;
        margin-top: 15px;
        margin-left: 10%;
        border: none;
        outline: none;
        background-color: transparent;
        display: block;
    }
    .search-clear{
      display: inline-block;
      width: 14px;
      height: 14px;
    background-image: url(data:image/svg+xml;base64,);
    position: absolute;
    top: 8px;
    right: 9px;
    }
    } 
      hr{
        height: 1px;
        border: none;
      background-color: #eff0f1;
    } 
 }
</style>